import React, {useState, useEffect} from 'react';
import axios from 'axios';
import './App.css';

function App() {
  const[people, setPeople] = useState()

  useEffect(()=>{
    const apiUrl = 'http://127.0.0.1:8000/api/test-api/'
    axios.get(apiUrl).then((resp)=>{
      let allPeople = resp.data
      setPeople(allPeople)
    })
  },[setPeople])

  return (
    <div className="App">
      <p>Hello world!</p>
      <ul>
        {people.map(i =>(
          <li key={i.id}>{i.name}</li>
        ))}
      </ul>
    </div>
  );
}

export default App;
